.usersTab{
    height: 100%;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.paginator{
    height: 40px;
    width: 100%;
    text-align: center;
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
}
.usersField{
    display: flex;
    justify-content: center;
    align-items: flex-start;
}
.usersTitle{
    height: 68px;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    font-size: 30px;
    font-weight: bold;
}